.patient-messages-container {
  width: 95vw;
  display: flex;
  flex-direction: row;
}
.patient-list-container {
  width: 30%;
  border-right: 1px solid grey;
}

.patient-list-item {
  border-bottom: 1px solid grey;
}
.patient-list-item > button {
  width: 100%;
  height: 5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: blueviolet;
}

.avatar > img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

.sender-details {
  display: flex;
  flex-direction: column;
}

.patient-message {
  width: 70%;
}

.patient-message > h3,
.patient-message > h4,
.patient-message > h5 {
  margin-top: 3px;
  margin-bottom: 3px;
}
.patient-message > h5 {
  font-weight: 400;
}
.patient-message {
  padding: 10px;
}

/* /Responsive design */

@media only screen and (max-width: 750px) {
  .avatar {
    display: none;
  }
  .sender-details {
    font-size: 0.6rem;
  }
}
@media only screen and (min-width: 751px) and (max-width: 835px) {
  .sender-details {
    font-size: 0.6rem;
  }
  .patient-message {
    padding: 10px;
  }
}
